.container{
    position: relative;
    top: 0;
    left: 0;
    width: 250px;
    height: 350px;
}
.photo{
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-medium);
    object-fit: cover;
    object-position: center center;
}

.favorite{
    position: absolute;
    top: -18px;
    right: -18px;
    padding: 0;
    background: transparent;
    border: none;
    cursor: pointer;
}
.favorite svg{
    width: 40px;
    height: 40px;
    stroke: black;
    object-fit: cover;
    object-position: center center;
    filter: drop-shadow(0px 0px 2px black);
}